<template>
	<div class="purchase-function">
		<div class="function-box">
			<div>
				<p class="profit-title">待入库商品数</p>
				<p class="profit-value">{{zongjiage}}</p>
			</div>
			<div>
				<p class="profit-title">已入库商品数</p>
				<p class="profit-value">{{WareList.length}}</p>
			</div>
			<div>
				<p class="profit-title">欠货供应商数</p>
				<p class="profit-value">{{qianGongNum}}</p>
			</div>
		</div>
		<div class="function-box">
			<div v-for="(item,index) in profitArrTitle" :key="index" class="profit-btns" @click="profitClick(index)">
				{{item}}
			</div>
		</div>
	</div>
</template>

<script>



export default {
  name: 'purchaseFunctionNav',
  data(){
	return{
		profitArrTitle:[
			'业务日期',
			'商品名称',
			'商品货号',
		],
		waitInNum:0,
		yiInNum:0,
		qianGongNum:0,
	}
  },
  methods:{
	profitClick(index){
		this.$emit("profitClick",index)
	},
	//计算总值
	MatchAllRes(key,data){
		let res=0
		data.forEach(obj=>{
			res+=parseInt(obj[key])
		})
		return res
	}
  },
  props:{
	WareList:{
		type:Array,
		default(){
			return []
		}
	}
  },
  computed:{
	allOrderMoney(){
		let res=0
		this.WareList.forEach(item=>{res+=item.num*item.price})
		return res
	},
	zongjiage(){
		let res=0
		//获取总商品数量
		this.WareList.forEach(obj=>{
			res+=parseFloat(obj.num*obj.price)
		})
		return res.toFixed(2)
	}
  },
  watch:{
	WareList(newVal){
		//获取总商品数量
		this.yiInNum=this.MatchAllRes('num',newVal)
	}
  },
  created(){
	console.log(this.WareList)
  }
}
</script >

